                   0 0 10px rgba(255, 255, 0, 0.3);
    }
    100% {
        text-shadow: 0 0 12px rgba(255, 255, 0, 0.7), /*  */
                   0 0 20px rgba(255, 255, 0, 0.5),
                   0 0 25px rgba(255, 255, 0, 0.3);
    }
}


/* Main content layout -  */
.main-content {
  position: relative;
  display: flex; /*  */
  flex-direction: column; /*  */
  height: 100%; /*  */
  width: 100%; /*  */
  overflow-x: hidden; /*  */
  align-items: center; /*  */
  padding: 0px; /* 20px */
  gap: 0px; /* 20px */
  box-sizing: border-box; /*  */
}

/* Preview container for 3D models - 3D */
.preview-container {
  height: 60%; /* 390px */
  border: none; /*  */
  display: flex; /*  */
  width: calc(100% - 70px); /*  */
  align-items: center; /*  */
  justify-content: center; /*  */
  background-color: transparent; /*  */
  position: fixed;
  bottom: 1.2%;
  transform: translateY(-50%);
  right: 0%;
  transition: width 0.3s cubic-bezier(0.4, 0, 0.2, 1); /*  */
  will-change: width; /*  */
}

/*  .preview-container  */
/* LaserCutImageProcessor.jsx  .sidebar-open-for-preview  */
.sidebar-open-for-preview .preview-container {
  width: calc(100% - 250px);
  right: 0%;
  
}

