.info-button2 {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 24px;
    height: 24px;
    border: 2px solid #ddd;
    border-radius: 50%;
    cursor: pointer;
    position: absolute; /*  */
    top: 16.7px; /*  */
    left: calc(50% + 81px);
 /*  */
    transform: translateX(-50%); /*  */
    font-size: 28px;
    background: white;
    color: #333;
    box-shadow: 0 4px 15px rgba(0,0,0,0.1);
    /*  */
    
    animation: lightning-flash 1.5s infinite;
    overflow: visible;
    transition: all 0.3s ease;
    z-index: 1000;
  }

  
  .info-button2:hover {
    transform: translateX(-50%) translateY(-2px);
    box-shadow: 0 6px 20px rgba(0,0,0,0.15);
    
    
  }
  
  .info-button2::before {
    content: 'i';
    font-size: 18px;
    font-weight: bold;
    font-style: italic;
    font-family: 'Times New Roman', serif;
    color: #333;
    animation: lightning-flicker 1.5s infinite;
  }
  
  .info-button2::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%; /*  */
    height: 100%; /*  */
    border: 2px solid rgba(255,255,255,0.5);
    border-radius: 50%;
    transform: translate(-50%, -50%) scale(0);
    animation: ripple-wave 1.5s infinite;
  }
  
  /*  */
  .info-button2.stopped {
    animation: none;
  }
  
  .info-button2.stopped::before {
    animation: none;
    text-shadow: 0 0 5px rgba(255,255,255,0.5);
    color: #333;
  }
  
  .info-button2.stopped::after {
    animation: none;
    opacity: 0;
  }
  
  /*  */
  @keyframes lightning-flash {
    0%, 90%, 100% { 
        box-shadow: 0 4px 15px rgba(255,255,255,0.3); 
        border-color: #ffffff; 
    }
    5%, 10% { 
        box-shadow: 0 4px 30px rgba(255,255,255,0.8); 
        border-color: #ffffff; 
        background: #ffffff; 
    }
  }
  
  @keyframes lightning-flicker {
    0%, 90%, 100% { 
        text-shadow: 0 0 5px rgba(255,255,255,0.5); 
        color: #333; 
    }
    5%, 10% { 
        text-shadow: 0 0 18px rgba(255,255,255,0.9); 
        color: #ffffff; 
    }
  }
  
  @keyframes ripple-wave {
    0% { 
        transform: translate(-50%, -50%) scale(0);
        opacity: 1;
        border-color: rgba(255,255,255,0.7);
    }
    10% { 
        transform: translate(-50%, -50%) scale(1);
        opacity: 0.8;
        border-color: rgba(255,255,255,0.7);
    }
    100% { 
        transform: translate(-50%, -50%) scale(2.5);
        opacity: 0;
        border-color: rgba(255,255,255,0);
    }
  }

