/* SVGCSS - ThreeDModelGenerator.css */

.processing-overlay-svg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  backdrop-filter: blur(8px);
  animation: fadeInOverlay 0.3s ease-out forwards;
}

@keyframes fadeInOverlay {
  from { opacity: 0; }
  to { opacity: 1; }
}

.processing-modal-svg {
  background: linear-gradient(160deg, #1a1a1a, #0d0d0d);
  border-radius: 25px;
  padding: 45px;
  max-width: 550px;
  width: 90%;
  box-shadow: 0 15px 40px rgba(0, 255, 255, 0.2), 0 0 60px rgba(0, 255, 255, 0.1);
  border: 1.5px solid #00ffff;
  text-align: center;
  color: #e0e0e0;
  animation: scaleInModal 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) forwards;
}

@keyframes scaleInModal {
  from { transform: scale(0.8); opacity: 0; }
  to { transform: scale(1); opacity: 1; }
}

.processing-content-svg h3 {
  color: #ffffff;
  margin-bottom: 35px;
  font-size: 1.8rem;
