.App {
  text-align: center; /* App */
}

.App-logo {
  height: 40vmin; /* App40% */
  pointer-events: none; /* App */
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear; /* App */
  }
}

.App-header {
  background-color: #282c34; /* App */
  min-height: 100vh; /* App100% */
  display: flex; /* App */
  flex-direction: column; /* App */
  align-items: center; /* App */
  justify-content: center; /* App */
  font-size: calc(10px + 2vmin); /* App10px2% */
  color: white; /* App */
}

.App-link {
  color: #61dafb; /* App */
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg); /* App */
  }
  to {
    transform: rotate(360deg); /* App */
  }
}

